.palette-list li[data-color="coyote"] {
    background-color: #759ab3;
    color: #4f4f4f;
}

.palette-list li[data-color="canada"] {
    background-color: #eb2d37;
    color: #4f4f4f;
}

.palette-list li[data-color="black"] {
    background-color: #060606;
    color: #f9f9f9;
}

.palette-list li[data-color="mineshaft"] {
    background-color: #2b2b2b;
    color: #f9f9f9;
}

.palette-list li[data-color="kaiser"] {
    background-color: #4f4f4f;
    color: #f9f9f9;
}

.palette-list li[data-color="dove"] {
    background-color: #737373;
    color: #f9f9f9;
}

.palette-list li[data-color="alto"] {
    background-color: #dddddd;
    color: #4f4f4f;
}

.palette-list li[data-color="white"] {
    background-color: #f9f9f9;
    color: #4f4f4f;
}

.palette-list li h3, .palette-list li .lede {
    margin-bottom: 0;
    line-height: 1.875;
    line-height: var(--line-height-default);
}

.measure-example {
    background-color: #dddddd;
    background-color: var(--color-alto);
    background-image: linear-gradient(to bottom, #eb2d37, #eb2d37);
    background-position: center center;
    background-size: 100% 0.125rem;
    background-repeat: no-repeat;
    height: 1.25rem;
    display: block;
}

.measure-example--small {
    width: 0.625rem;
}

.measure-example--medium {
    width: 1.25rem;
}

.measure-example--large {
    width: 2.25rem;
}

.measure-example--one {
    width: 1rem;
}

.measure-example--responsive {
    width: calc(336px + (480 - 336) * ((100vw - 500px) / (1140 - 500)));
}

.transition-example {
    position: relative;
    margin-bottom: 3.5rem;
}

.transition-example::before {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    content: "(Hover to interact.)";
    color: #4f4f4f;
    color: var(--color-kaiser);
    margin-top: 0.625rem;
    margin-top: var(--measure-small);
}

@media (any-pointer: coarse) {
    .transition-example::before {
        content: "(Tap to interact.)";
    }
}

.transition-example__demo {
    background-clip: padding-box;
    border-radius: 0.25rem;
    transition: color 0.2s ease 0s;
    width: 100%;
    max-width: calc(336px + (480 - 336) * ((100vw - 500px) / (1140 - 500)));
    max-width: var(--line-length-responsive);
    height: 3.375rem;
    display: block;
    border-width: 0.125rem;
    border-width: var(--border-width-thin);
    border-style: solid;
    border-style: var(--border-style-default);
    border-color: #dddddd;
    border-color: var(--color-alto);
    margin-left: auto;
    margin-right: auto;
    overflow-x: hidden;
    position: relative;
    line-height: 3.125;
    text-align: center;
    z-index: 2;
    z-index: var(--z-index-above);
}

.transition-example__demo:hover, .transition-example__demo:focus {
    color: #f9f9f9;
    color: var(--color-white);
}

.transition-example__demo::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-clip: padding-box;
    border-radius: 0.125rem;
    content: "";
    background-color: #4f4f4f;
    background-color: var(--color-kaiser);
    z-index: -1;
    z-index: var(--z-index-below);
}

.transition-example--slide .transition-example__demo {
    cursor: e-resize;
}

.transition-example--slide .transition-example__demo::before {
    transition: transform 0.2s ease 0s;
    transform: translateX(-101%);
}

.transition-example--slide .transition-example__demo:hover::before, .transition-example--slide .transition-example__demo:focus::before {
    transform: translateX(0);
}

.transition-example--fade .transition-example__demo {
    cursor: cell;
}

.transition-example--fade .transition-example__demo::before {
    transition: opacity 0.2s ease 0s;
    opacity: 0;
}

.transition-example--fade .transition-example__demo:hover::before, .transition-example--fade .transition-example__demo:focus::before {
    opacity: 1;
}

.icons-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.icons-list li {
    display: inline-block;
    margin-left: 1.25rem;
    margin-left: var(--measure-medium);
    margin-right: 1.25rem;
    margin-right: var(--measure-medium);
}

.christmas {
    --color-raven: green;
    --color-bowhead: red;
    --color-coyote: green;
}

.halloween {
    --color-black: invert($color-black);
    --color-mineshaft: invert($color-mineshaft);
    --color-kaiser: invert($color-kaiser);
    --color-dove: invert($color-dove);
    --color-alto: invert($color-alto);
    --color-white: invert($color-white);
    --color-raven: #ff751a;
    --color-bowhead: #f56200;
    --color-coyote: #ff8b3d;
}
